import {AlertMethod} from "../alert/Alert";
import YAML from 'yaml'

export const FetchHeaders = {'Content-Type':'application/json'}

/**
 * Use method in requestOptions to communicate with api server.
 *
 * @param requestOptions contains http method. headers and body.
 * @param {string} serverUrl is a relative or points to your development server.
 * @function sendRequest
 * @returns [AlertMethod, AlertMessage]
 */
export async function sendRequest(requestOptions, serverUrl) {
    // POST request using fetch with async/await
    try {
        const response = await fetch(serverUrl, requestOptions);
        if (!response.ok) {
            return [AlertMethod.Error, 'The Operator API returns not OK Response.'];
        }

        const data = response.status;
        console.log(response);
        console.log(JSON.stringify(data));
        return [AlertMethod.Success, 'Success!'];
    } catch (error) {
        console.log(error);
        return [AlertMethod.Error, error.toString()];
    }
}


/**
 * Use GET method to get data
 *
 * @param {string} serverUrl is a relative or points to your development server.
 * @function getRequest
 * @returns [AlertMethod, AlertMessage, Result in JSON Object(Optional)]
 */
export async function getRequest(serverUrl){
    try {
        const response = await fetch(serverUrl);
        if (!response.ok) {
            return [AlertMethod.Error, 'The Operator API returns not OK Response.'];
        }
        const result = await response.json();
        console.log(result)
        return [AlertMethod.Success, 'Success!', result];
    } catch (error) {
        console.log(error);
        return [AlertMethod.Error, error.toString()];
    }
}

/**
 * Check the input str is JSON or not
 *
 * @param {string} str is a JSON string
 * @function isJSONString
 * @returns {boolean} bool
 */
export function isJSONString(str) {
    try {JSON.parse(str)}
    catch (e) { return false}
    return true;
}

/**
 * Convert YAML String to JSON String
 *
 * @param {string} yamlStr is a YAML string
 * @function YAMLtoJSON
 * @returns {string} jsonStr
 */
export function YAMLtoJSON(yamlStr) {
    const obj = YAML.parse(yamlStr);
    return JSON.stringify(obj);
}

/**
 * Convert JSON String to YAML String
 *
 * @param {string} jsonStr is a YAML string
 * @function JSONtoYAML
 * @returns {string} yamlStr
 */
export function JSONtoYAML(jsonStr) {
    const obj = JSON.parse(jsonStr);
    return YAML.stringify(obj);
}

export function validateInputConfigAndParseAsJSON(input) {
    try {
        const jsonObject = JSON.parse(input);
        return JSON.stringify(jsonObject);
    } catch (e) {
    // If JSON parsing fails, try YAML
        try {
            const yamlObject = YAML.parse(input)
            return JSON.stringify(yamlObject);
        } catch (yamlError) {
            return 'ERROR'
        }
    }
}

