import * as React from "react";
import * as ReactDOM from "react-dom/client";
import {
    createBrowserRouter,
    RouterProvider,
} from "react-router-dom";
import "./index.css";
import DatasetController from "./pages/dataset-controller";
import HomePage from "./pages/homepage";
import AlluxioController from "./pages/alluxio-controller";

const router = createBrowserRouter([
    {
        path: "/",
        element: <HomePage/>,
    },
    {
        path: "/Alluxio_Cluster",
        element: <AlluxioController/>,
    },
    {
        path: "/Alluxio_Dataset",
        element: <DatasetController/>,
    },
]);

// @ts-ignore
ReactDOM.createRoot(document.getElementById("root")).render(
    <React.StrictMode>
        <RouterProvider router={router} />
    </React.StrictMode>
);